import axios from 'axios'
axios.defaults.baseURL = '/portal'
axios.defaults.headers['X-Requested-With'] = 'XMLHttpRequest'
axios.defaults.headers.post['Content-Type'] = 'application/json'
axios.defaults.transformRequest = [data => {
  return JSON.stringify(data)
}]
axios.defaults.timeout = 5000

// 登录
export const requestLogin = data => { return axios.post('/baseInter/login.do', data) }
// 注册
export const requestRegist = data => { return axios.post('/baseInter/register.do', data) }
// 获取用户信息
export const getMyInfo = () => { return axios.get('/accountInter/getMyInfo.do') }
// 获取手机短信验证码
export const getMobileSmsCode = params => {return axios.get('/baseInter/getMobileSmsCode.do', {params: params})}
// 查询邮箱或手机号是否已被使用
export const accountFind = params => { return axios.get('/baseInter/accountFind.do', {params: params}) }
// 加盟信息
export const createJoinInfo = data => { return axios.post('/baseInter/createJoinInfo.do', data) }


